}

.about-section {
  font-size: 40px;
  text-align: center;
  padding: 50px 0; /* Adjust padding for spacing */
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  /* color: white; */
  color: #c0ca50; /* Hot Pink */

  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  background-image: linear-gradient(rgba(0, 0, 50, 0.8), rgba(0, 0, 50, 0.8)),
    url("media/retro-anime-1grctabx31sxl8oo.jpg");
  background-position: center;
  background-size: cover;
  position: relative;
}

@keyframes slideInLeft {
  0% {
    transform: translateY(-5%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes scaleIn {
  from {
    transform: scale(0.4);
  }
  to {
    transform: scale(1);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}

