.l-container {
  text-align: center;
}

h1 {
  margin-bottom: 20px; /* Adjust the margin-bottom to control the space between heading and cards */
}

.b-game-cards-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.b-game-card {
  margin: 0 10px; /* Adjust the margin to control the space between the cards */
  perspective: 1000px; /* Set the perspective for the 3D effect */
}

.b-game-card__cover {
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.5s;
}

.b-game-card:hover .b-game-card__cover {
  transform: rotateY(180deg); /* Flip the card on hover */
}

.b-game-card__cover img {
  max-width: 100%;
  height: auto;
  display: block;
  max-height: 550px; /* Adjust the max-height to control the size of the images */
  backface-visibility: hidden; /* Hide the back of the image during the flip */
  border-radius: 50px;
}

.b-game-card__back {
  transform: rotateY(180deg); /* Initially hide the back of the card */
  position: absolute;
  font-size: 50px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  backface-visibility: hidden; /* Hide the back during the flip */
  background-color: #ee7a36; /* Back face color, adjust as needed */
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333; /* Text color on the back face */
  border-radius: 50px;
}

